@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap');

#contani{
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: space-evenly;
}

.contai{
    width:fit-content;
    position: relative;
    display: flex;
    padding-top: 16%;
}

.column-content{
    display: flex;
    width: fit-content;
    height: fit-content;
    /* align-items: center; */
    justify-content: center;
}

.contai .card{
    position: relative;
    border: none;
    background: transparent;
}

.contai .card .face{
    width: 300px;
    height: 200px;
    border-radius: 10px;
    transition: 500ms;
}

.contai .card .face.face1{
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
}

.contai .card:hover .face.face1{
    background: #ff5555;
    transform: translateY(0);
}

.contai .card .face.face1 .content{
    opacity: 1;
    transition: 500ms;
}

.contai .card:hover .face.face1 .content{
    opacity: 0.9;
}

.contai .card .face.face1 .content img{
    max-width: 100px;
}

.contai .card .face.face1 .content h3{
    margin: 10px 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
    font-family: 'Inconsolata', monospace;
    font-weight: 600;
}

.contai .card .face.face2{
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0,0,0,0.8);
    transform: translateY(-100px);
} 

.contai .card:hover .face.face2{
    transform: translateY(0);
}

.contai .card .face.face2 .content p{
    margin: 0;
    padding: 0;
    font-family: 'Dosis', sans-serif;
    font-weight: 400;
}