@-webkit-keyframes rotate{
    25%{
        -webkit-transform: rotate(90deg) scale(.25);
                transform: rotate(90deg) scale(.25);;
    }
    50%{
        -webkit-transform: (180deg) scale(.5);
                transform: (180deg) scale(.5);
    }
    75%{
        -webkit-transform: rotate(270deg) scale(.75);
                transform: rotate(270deg) scale(.75);
    }
    100%{
        -webkit-transform: (360deg);
                transform: (360deg);
    }
}

@keyframes rotate{
    25%{
        -webkit-transform: rotate(90deg) scale(.25);
                transform: rotate(90deg) scale(.25);;
    }
    50%{
        -webkit-transform: (180deg) scale(.5);
                transform: (180deg) scale(.5);
    }
    75%{
        -webkit-transform: rotate(270deg) scale(.75);
                transform: rotate(270deg) scale(.75);
    }
    100%{
        -webkit-transform: (360deg);
                transform: (360deg);
    }
}
#contani{
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: space-evenly;
}

.contai{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    position: relative;
    display: flex;
    padding-top: 16%;
}

.column-content{
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* align-items: center; */
    justify-content: center;
}

.contai .card{
    position: relative;
    border: none;
    background: transparent;
}

.contai .card .face{
    width: 300px;
    height: 200px;
    border-radius: 10px;
    transition: 500ms;
}

.contai .card .face.face1{
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
}

.contai .card:hover .face.face1{
    background: #ff5555;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.contai .card .face.face1 .content{
    opacity: 1;
    transition: 500ms;
}

.contai .card:hover .face.face1 .content{
    opacity: 0.9;
}

.contai .card .face.face1 .content img{
    max-width: 100px;
}

.contai .card .face.face1 .content h3{
    margin: 10px 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
    font-family: 'Inconsolata', monospace;
    font-weight: 600;
}

.contai .card .face.face2{
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0,0,0,0.8);
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
} 

.contai .card:hover .face.face2{
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.contai .card .face.face2 .content p{
    margin: 0;
    padding: 0;
    font-family: 'Dosis', sans-serif;
    font-weight: 400;
}
